/*  eslint-disable */
import React, { Component } from "react";
import { Link, withRouter } from "react-router-dom";
import AdminSidebar from "../../components/AdminSidebar";
import "../../asserts/commoncss/sidebar.css";
import Config from "../../controllers/Config";
import moment from "moment";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import {
  faTrash,
  faPenAlt,
  faEye,
  faPlus,
  faWindowClose,
  faDownload,
} from "@fortawesome/free-solid-svg-icons";
import { getAllProducts, deleteProduct } from "../../controllers/Products";
import { getAllCategories } from "../../controllers/Category";
import { connect } from "react-redux";
import jsPDF from "jspdf";
import "jspdf-autotable";
import { DateRangePicker } from "react-dates";
import "react-dates/initialize";
import "react-dates/lib/css/_datepicker.css";

class AdminProduct extends Component {
  constructor(props) {
    super(props);
    this.state = {
      products: [],
      active_category: "All",
      categories: ["All"],
      startDate: null,
      endDate: null,
      focused: null,
    };
  }

  componentDidMount() {
    this.loadProducts();
    this.loadCategories();
  }

  loadProducts = () => {
    getAllProducts()
      .then((result) => {
        console.log(result);
        this.setState({ products: result });
      })
      .catch((err) => {
        console.log(err);
      });
  };

  loadCategories = () => {
    getAllCategories()
      .then((result) => {
        this.setState({
          categories: [...this.state.categories, ...result.map((c) => c.name)],
        });
      })
      .catch((err) => {
        console.log(err);
      });
  };

  render() {
    const { products, categories, active_category } = this.state;

    return (
      <div className="bg-light wd-wrapper">
        <AdminSidebar active={"products"} />
        <div className="wrapper-wx">
          <div className="container-fluid">
            <div className="row">
              <div className="col-12">
                <h5 className="text-dark bold-normal py-2 bg-white shadow-sm px-2 mt-3 rounded">
                  Product Categories
                </h5>
              </div>
              {/* ----------------------------------------------------------- */}
              <div className="col-12">
                <div className="card border-0 shadow-sm rounded mt-3 bg-white pb-2">
                  <div className="row m-1 p-1">
                    <button
                      className="btn btn-success btn-sm px-2 mr-2 mt-1"
                      onClick={() => this.downloadAllProducts()}
                    >
                      <FontAwesomeIcon icon={faDownload} /> Download Report
                    </button>
                    <DateRangePicker
                      startDate={this.state.startDate} // momentPropTypes.momentObj or null,
                      startDateId="your_unique_start_date_id" // PropTypes.string.isRequired,
                      endDate={this.state.endDate} // momentPropTypes.momentObj or null,
                      endDateId="your_unique_end_date_id" // PropTypes.string.isRequired,
                      onDatesChange={({ startDate, endDate }) =>
                        this.setState({ startDate, endDate })
                      } // PropTypes.func.isRequired,
                      focusedInput={this.state.focused} // PropTypes.oneOf([START_DATE, END_DATE]) or null,
                      onFocusChange={(focusedInput) =>
                        this.setState({ focused: focusedInput })
                      }
                      isOutsideRange={() => false}
                      showClearDates={true} // PropTypes.func.isRequired,
                    />
                  </div>
                </div>
                <div className="card border-0 shadow-sm rounded mt-3 bg-white pb-2">
                  <h5 className="text-dark bold-normal py-2 px-2 mb-0 mt-2">
                    {categories.map((c, i) => {
                      return (
                        <span
                          key={i}
                          onClick={() => this.setState({ active_category: c })}
                          className={`badge mx-2 px-2 ${
                            active_category == c
                              ? "badge-info click"
                              : "border text-muted click"
                          }`}
                        >
                          {c}
                        </span>
                      );
                    })}
                  </h5>
                  <div className="table-responsive px-2 pt-2">
                    <table className="table table-stripped">
                      <thead>
                        <tr>
                          <th scope="col">Images</th>
                          <th scope="col">Name</th>
                          <th scope="col">Category</th>
                          <th scope="col">Price</th>
                          <th scope="col">Created At</th>
                          <th scope="col">Actions</th>
                        </tr>
                      </thead>
                      <tbody>
                        {this.filterByCategory().map((item) =>
                          this.renderProductTableItem(item)
                        )}
                      </tbody>
                    </table>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    );
  }

  filterByCategory = () => {
    const { active_category, products } = this.state;
    if (active_category == "All") {
      return products;
    } else {
      return products.filter((p) => p.category_name == active_category);
    }
  };

  renderProductTableItem = (item) => {
    return (
      <tr key={item._id}>
        <td>
          <img
            src={Config.setImage(item.images[0])}
            width={50}
            className="mr-2"
          />
          {item.images.length > 1 && (
            <img src={Config.setImage(item.images[1])} width={40} />
          )}
        </td>
        <td>
          <h6 className="form-label mb-2">{item.name}</h6>
          {item.sizes.map((size, i) => (
            <span key={i} className="mr-2 border border-muted px-2 text-muted">
              {size.label}
            </span>
          ))}
        </td>
        <td>
          <h6 className="">{item.category_name}</h6>
        </td>
        <td>
          <h6 className="form-label ">LKR {item.price}</h6>
        </td>
        <td>{moment(new Date(item.created_at)).format("YYYY MMM DD")}</td>
        <td>
          <button
            onClick={() => this.onClickView(item)}
            className="btn btn-success btn-sm px-2 mr-2"
          >
            <FontAwesomeIcon icon={faEye} />
          </button>
          <button
            className="btn btn-secondary btn-sm px-2 mr-2"
            onClick={() => this.onClickUpdate(item)}
          >
            <FontAwesomeIcon icon={faPenAlt} />
          </button>
          <button
            className="btn btn-danger btn-sm px-2 mr-2"
            onClick={() => this.onClickDelete(item)}
          >
            <FontAwesomeIcon icon={faTrash} />
          </button>
        </td>
      </tr>
    );
  };

  onClickView = (item) => {
    this.props.history.push(`/product/${item._id}`);
  };

  onClickUpdate = (item) => {
    this.props.history.push(`/admin/products/update/${item._id}`);
  };

  onClickDelete = (item) => {
    Config.setDeleteConfirmAlert(
      "Delete",
      "Are you sure you want to delete this Product ?",
      () => this.clickDeleteProduct(item._id),
      () => {}
    );
  };

  clickDeleteProduct = (id) => {
    console.log(id);
    deleteProduct(id, this.props.auth.user.token, this.props.auth.user.type)
      .then((result) => {
        this.loadProducts();
        Config.setToast(" Product Deleted Successfully");
        console.log("Success");
      })
      .catch((err) => {
        console.log(err);
        Config.setErrorToast(" Somthing Went Wrong!");
      });
  };

  downloadAllProducts = () => {
    if (this.state.startDate == null || this.state.endDate == null) {
      alert("Select Date Range Please!");
    } else {
      let data = [];
      let count = 1;
      let col = [
          { dataKey: "count", header: "Count" },
          { dataKey: "name", header: "Name" },
          { dataKey: "category", header: "Category" },
          { dataKey: "price", header: "Price" },
          { dataKey: "cdate", header: "Created Date" },
        ],
        doc = new jsPDF();
      doc.page = 1;
      let sdate = moment(new Date(this.state.startDate)).format("YYYY MMM DD");
      let edate = moment(new Date(this.state.endDate)).format("YYYY MMM DD");
      var header = function () {
        var imgData =
          "data:image/png;base64,";

        // doc.fontStyle("normal");

        // move_from_left, move_from_height, width, height
        doc.addImage(imgData, "JPEG", 85, 10, 43, 12);

        doc.setFontSize(13.5);
        // doc.fontStyle("bold");

        // move_from_left, move_from_height
        doc.text(92, 35, "Product Report");
        doc.setFontSize(12);
        doc.text(80, 40, sdate + " - " + edate);
      };

      var options = {
        beforePageContent: header,

        theme: "striped",
        columnStyles: {
          count: { columnWidth: 20 },
          name: { columnWidth: 70 },
          category: { columnWidth: 40 },
          price: { columnWidth: 30 },
          cdate: { columnWidth: 30 },
        },

        headStyles: { fillColor: "white", textColor: "black" },
        style: { cellWidth: "auto" },
        margin: { top: 50 },
      };

      // Data Processing
      this.state.products.map((item) => {
        let b = {
          count: count,
          name: item.name,
          category: item.category_name,
          price: item.price,
          cdate: moment(new Date(item.created_at)).format("YYYY MMM DD"),
        };

        var bool1 = moment(new Date(item.created_at)).isAfter(
          this.state.startDate
        );
        var bool2 = moment(new Date(item.created_at)).isBefore(
          this.state.endDate
        );
        console.log(bool1);
        console.log(bool2);
        if (bool1 && bool2) {
          data.push(b);
        }

        count++;
      });

      doc.setFontSize(12);
      // doc.line(0, 145, width, 145);

      doc.autoTable(col, data, options);

      doc.save("ProductReport.pdf");
    }
  };
}

const mapStateToProps = (state) => ({
  auth: state.auth || {},
});
export default connect(mapStateToProps)(withRouter(AdminProduct));
