import React, { Component } from "react";
import { withRouter } from "react-router-dom";
import AdminSidebar from "../../components/AdminSidebar";
import moment from "moment";
import {
  markRecevied,
  getAllOrdersWithDelivery,
} from "../../controllers/Delivery";
import jsPDF from "jspdf";
import "jspdf-autotable";
import { DateRangePicker } from "react-dates";
import "react-dates/initialize";
import "react-dates/lib/css/_datepicker.css";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faDownload } from "@fortawesome/free-solid-svg-icons";

class allDelivery extends Component {
  constructor(props) {
    super(props);
    this.state = {
      loading: true,
      orders: [],
    };
  }

  componentDidMount() {
    this.loadDelivery();
  }

  loadDelivery = () => {
    getAllOrdersWithDelivery()
      .then((result) => {
        console.log(result);
        this.setState({ orders: result });
      })
      .catch((err) => {
        console.log(err);
      });
  };

  getStyle = (item) => {
    console.log("Get Style: ", item.order_details);
    return {
      backgroundColor: item.order_details ? "#ffa1a1" : "#FFFFFF",
    };
  };

  render() {
    const { orders } = this.state;
    let reversedOrders = orders.reverse();

    let ShippedOrders = reversedOrders.filter((ord) => !ord.recieved);
    let DeliveredOrders = reversedOrders.filter((ord) => ord.recieved);

    return (
      <div className="bg-light wd-wrapper">
        <AdminSidebar active={"orders"} />
        <div className="wrapper-wx">
          <div className="container-fluid">
            <div className="row">
              <div className="col-12">
                <h5 className="text-dark bold-normal py-2 bg-white shadow-sm px-2 mt-3 rounded">
                  Deliveries
                </h5>
                {/* -------------------------------------------------- */}
                <div className="col-12 px-0">
                  <div className="card border-0 shadow-sm rounded mt-3 bg-white pb-2">
                    <h6 className="text-dark bold-normal py-2 bg-white px-2">
                      Shipped Orders
                    </h6>

                    <div className="table-responsive px-2">
                      <table className="table table-stripped">
                        <thead>
                          <tr>
                            <th scope="col">Date</th>
                            <th scope="col">User Name</th>
                            <th scope="col">Amount</th>
                            <th scope="col">Address</th>
                            <th scope="col">Method</th>
                            <th scope="col">Tracking No</th>
                            <th scope="col">Action</th>
                          </tr>
                        </thead>
                        <tbody>
                          {ShippedOrders.map((item) =>
                            this.renderOrdersTable(item)
                          )}
                        </tbody>
                      </table>
                    </div>
                  </div>
                </div>
                {/* -------------------------------------------------- */}

                <div className="col-12 px-0">
                  <div className="card border-0 shadow-sm rounded mt-3 bg-white pb-2">
                    <h6 className="text-dark bold-normal py-2 bg-white px-2">
                      Delivered Orders
                    </h6>

                    <div className="row m-1 p-1">
                      <button
                        className="btn btn-success btn-sm px-2 mr-2 mt-1"
                        onClick={() => this.downloadAllProducts()}
                      >
                        <FontAwesomeIcon icon={faDownload} /> Download Report
                      </button>
                      <DateRangePicker
                        startDate={this.state.startDate} // momentPropTypes.momentObj or null,
                        startDateId="your_unique_start_date_id" // PropTypes.string.isRequired,
                        endDate={this.state.endDate} // momentPropTypes.momentObj or null,
                        endDateId="your_unique_end_date_id" // PropTypes.string.isRequired,
                        onDatesChange={({ startDate, endDate }) =>
                          this.setState({ startDate, endDate })
                        } // PropTypes.func.isRequired,
                        focusedInput={this.state.focused} // PropTypes.oneOf([START_DATE, END_DATE]) or null,
                        onFocusChange={(focusedInput) =>
                          this.setState({ focused: focusedInput })
                        }
                        isOutsideRange={() => false}
                        showClearDates={true} // PropTypes.func.isRequired,
                      />
                    </div>
                    <div className="table-responsive px-2">
                      <table className="table table-stripped">
                        <thead>
                          <tr>
                            <th scope="col">Date</th>
                            <th scope="col">User Name</th>
                            <th scope="col">Amount</th>
                            <th scope="col">Address</th>
                            <th scope="col">Method</th>
                            <th scope="col">Tracking No</th>
                            <th scope="col">Action</th>
                          </tr>
                        </thead>
                        <tbody>
                          {DeliveredOrders.map((item) =>
                            this.renderOrdersTable(item)
                          )}
                        </tbody>
                      </table>
                    </div>
                  </div>
                </div>
                {/* ----------------------------------------------------- */}
              </div>
            </div>
          </div>
        </div>
      </div>
    );
  }

  updateReceived = (item) => {
    markRecevied({ id: item._id })
      .then((result) => {
        this.loadDelivery();
      })
      .catch((err) => {});
  };

  renderOrdersTable = (item) => {
    console.log(
      "----------------------------------------------------",
      item.order_details
    );

    return (
      <tr key={item._id} style={this.getStyle(item.order_details)}>
        <td>
          <b>
            {moment(new Date(item.order_details.date)).format("DD , MMM YYYY")}
          </b>
        </td>
        <td>
          <h6 className="form-label mt-1">LKR {item.order_details.amount}</h6>
        </td>
        <td>{item.order_details.userName}</td>
        <td>{item.order_details.deliveryAddress}</td>
        <td>{item.deliveryMethod}</td>
        <td>{item.TrackingInfo}</td>
        <td>
          {!item.recieved ? (
            <button
              className="btn btn-light btn-sm px-2 mr-2 btn-outline-dark"
              onClick={() => this.updateReceived(item)}
            >
              Mark Delivered
            </button>
          ) : (
            <span className="bg-success text-white px-2 py-1 rounded">
              Delivered
            </span>
          )}
        </td>
      </tr>
    );
  };

  downloadAllProducts = () => {
    const { orders } = this.state;
    let reversedOrders = orders.reverse();

    let DeliveredOrders = reversedOrders.filter((ord) => ord.recieved);

    if (this.state.startDate == null || this.state.endDate == null) {
      alert("Select Date Range Please!");
    } else {
      let data = [];
      let count = 1;
      let col = [
          { dataKey: "count", header: "Count" },
          { dataKey: "cdate", header: "Date" },
          { dataKey: "userName", header: "User Name" },
          { dataKey: "amount", header: "Amount" },
          { dataKey: "address", header: "Address" },
          { dataKey: "method", header: "Method" },
          { dataKey: "trackingNum", header: "Tracking Num" },
        ],
        doc = new jsPDF();
      doc.page = 1;
      let sdate = moment(new Date(this.state.startDate)).format("YYYY MMM DD");
      let edate = moment(new Date(this.state.endDate)).format("YYYY MMM DD");
      var header = function () {
        var imgData =
          "data:image/png;base64,";

        // doc.fontStyle("normal");

        // move_from_left, move_from_height, width, height
        doc.addImage(imgData, "JPEG", 85, 10, 43, 12);

        doc.setFontSize(13.5);
        // doc.fontStyle("bold");

        // move_from_left, move_from_height
        doc.text(92, 35, "Product Report");
        doc.setFontSize(12);
        doc.text(80, 40, sdate + " - " + edate);
      };

      var options = {
        beforePageContent: header,

        theme: "striped",
        columnStyles: {
          count: { columnWidth: 15 },
          cdate: { columnWidth: 20 },
          userName: { columnWidth: 25 },
          amount: { columnWidth: 20 },
          address: { columnWidth: 60 },
          method: { columnWidth: 20 },
          trackingNum: { columnWidth: 30 },
        },

        headStyles: { fillColor: "white", textColor: "black" },
        style: { cellWidth: "auto" },
        margin: { top: 50 },
      };
      console.log(DeliveredOrders);

      // Data Processing
      DeliveredOrders.map((item) => {
        let b = {
          count: count,
          cdate: moment(new Date(item.order_details.date)).format(
            "YYYY MMM DD"
          ),
          userName: item.order_details.userName,
          amount: item.order_details.amount,
          address: item.order_details.deliveryAddress,
          method: item.deliveryMethod,
          trackingNum: item.TrackingInfo,
        };
        console.log(b);
        var bool1 = moment(new Date(item.order_details.date)).isAfter(
          this.state.startDate
        );
        var bool2 = moment(new Date(item.order_details.date)).isBefore(
          this.state.endDate
        );

        console.log(bool1);
        console.log(bool2);
        if (bool1 && bool2) {
          data.push(b);
        }

        count++;
      });

      doc.setFontSize(12);
      // doc.line(0, 145, width, 145);

      doc.autoTable(col, data, options);

      doc.save("ProductReport.pdf");
    }
  };
}
export default withRouter(allDelivery);
